<template>
  <div class="App">
    <div @click="changeDesignMode">
        <a href="#">{{designMode ? 'Run' : 'Design' }}</a>
    </div>
	<div @click="changeFullscreen">
		<a href="#">Fullscreen</a>
	</div>
	<div id="Archi2D"> <!-- TODO étudier le style fullscreen TOUT EN BAS du code -->
		<ion-phaser 
		v-bind:game.prop="game"
		/> 
	</div>
	<div @click="showLayers">
        <a href="#">Display Layers</a>
    </div>   
</div>
</template>

<script>

let debug = true //TODO IN PRODUCTION => debug = false 

let JSON_resources =
{
projet: 'Projet_1',
items:
[
    {
        id: 'background', type: 'image', src:'user_download/COUPE1-1024x764.png',
    },
    {
        id: 'man_idle', type: 'spritesheet',src:'fixed/man_idle_68x150.png',
        frameWidth: 68, frameHeight: 150,
    },
    {
        id: 'man04', type:'spritesheet', src:'animated/man04_78x150.png',
        frameWidth: 78, frameHeight: 150,
    }
]
}

let JSON_properties =
{
	projet: 'Projet_1',
    items:
    [
        {
            id: 'man_idle', x: 255, y: 291, tint: 0x000000, alpha: 0.7, flipX: true, scale: 0.31,
        },
        {
            id: 'man_idle', x: 230, y: 600, tint: 0x000000, alpha: 1, flipX: false, scale: 0.8,
        },
        {
            id: 'man04', x: 470, y: 587, tint: 0x000000, alpha: 0.5, flipX: false, scale: 0.37,
            // tweenX: 250, tweenDuration:8000, tweenFlipX:false, tweenYoyo: true,                    
        },
        {
            id: 'man04', x: 150, y: 587, tint: 0x000000, alpha: 1, flipX: true, scale: 0.39,
            // tweenX: 520,tweenDuration:8000,tweenFlipX:false, tweenYoyo: true,
        },
        {
            id: 'man04', x: 706, y: 87, tint: 0x000000, alpha: 1, flipX: false, scale: 0.37,
            // tweenX: 0,tweenDuration:0,tweenFlipX:0, tweenYoyo: 0,
        }
    ],
    layers:
    [
        { 
            name: 'Layer_1', 
            invertAlpha: false,
            color: '0x77B5FE', 
            zones: [ { id: 'Bloc Electrique', coords: [ 210, 0, 45, 350 ] } ],
        },
        { 
            name: 'Layer_2', 
            invertAlpha: true,
            color: '0xFFA500', 
            zones: [  { id: 'Terrasse', coords: [ 190, 100, 508, 30 ] }, { id: 'Garage', coords: [ 502, 30, 104, 80 ] }, { id: 'Sous-Sol', coords: [ 387, 550, 103, 65 ] } ],
        },
        
    ],
}


// TODO : sceneWidth et sceneHeight en paramètres
let sceneWidth = 1024
let sceneHeight = 764

let sprites = []
let spritesShape = []
let animations = []

let graphics = []
let graphicsMask = []
let masks = []
let layers = []

let displayLayers = false
let designMode = true

let polygon = null
let fullscreen = false

import Phaser from 'phaser'
export default {
  name: 'Archi2D',
  data() {
    return {
		designMode: true,
		game: {
			width: sceneWidth,
			height: sceneHeight,
			type: Phaser.WEBGL,
			antialias: true,
			backgroundColor: 0xF5F5DC,
			scale: {
				parent: 'Archi2D',
				mode: Phaser.Scale.FIT,		
				width: sceneWidth,
				height: sceneHeight
			},
			autoRound: false,
			scene: {
			preload: function()
			{  
				// Load resources
				this.load.crossOrigin = 'anonymous'
				this.load.setPath('assets/')
				for (let i = 0; i < JSON_resources.items.length; i++)
				{ 
					const resource = JSON_resources.items[i]
					switch (resource.type) 
					{
					case 'image':
						this.load.image(resource.id, resource.src)
						break;
					case 'spritesheet':
						this.load.spritesheet(resource.id, resource.src, { frameWidth:resource.frameWidth, frameHeight:resource.frameHeight })
						break;
					}
				}

			},
			create: function()
			{   
				/* THERE MUST BE a background image */
				this.background = this.add.image(512, 384, 'background')

				// Create spritesheet anims => ANIMATIONS              
				for (let i = 0; i < JSON_resources.items.length; i++)
				{ 
					const resource = JSON_resources.items[i]
					if (resource.type === 'spritesheet')
					{
						this.anims.create(
						{
							key: resource.id + '_Animation',
							frames: this.anims.generateFrameNumbers(resource.id),
							frameRate: 10,
							repeat: -1
						}
						)
					}
				}

					
				// Create spritesheet anims => ANIMATIONS + sprite properties
				// .stop() by default
				for (let i = 0; i <JSON_properties.items.length; i++)
				{
					const sprite = JSON_properties.items[i]
					let s = this.add.sprite( sprite.x, sprite.y, sprite.id +'_' + i)
					.setFlipX(sprite.flipX)
					.setScale(sprite.scale)
					.setTintFill(sprite.tint) 
					.setAlpha(sprite.alpha)
					.play(sprite.id + '_Animation')
					.stop()
					
					
					s.setInteractive({ draggable: true })
					.on('pointerdown', function ()
					{      
						this.setTintFill(0x0000aa).setAlpha(0.2)
						//alert(this.width)
						
						//this.add.rectangle(sprites[i].x, sprites[i].y, 90,90, 0x0000ff).setAlpha(1)
						//this.clearTint()
						// pointerdown
						// console.log(gameObject.toJSON())
						// this.emit('clicked', this)
						
					})
					.on('pointerup', function ()
					{
						// s.clearTint()
						//this.add.rectangle(sprites[i].x, sprites[i].y, 90,90, 0x0000ff).setAlpha(1)
						//this.clearTint()
						// pointerdown
						// console.log(gameObject.toJSON())
						// this.emit('clicked', this)
						
					})
					sprites.push (s)      
					
					// draw rectangle around sprite
					let g = null
					g = this.add.graphics({ fillStyle: { color: 0x77B5FE }, lineStyle: {  width: 2, color: 0x77B5FE, alpha: 1 } })
					const bounds = sprites[i].getBounds()
					g.strokeRectShape(bounds)
					g.setVisible(true) 
					spritesShape.push(g)
					
					const rect = new Phaser.Geom.Rectangle(bounds.x, bounds.y, bounds.width, bounds.height)
					const points = []
					Phaser.Geom.Rectangle.Decompose(rect, points);
					for(let j = 0; j < points.length; j++)
					{
						g.fillCircle(points[j].x, points[j].y, 3);
					}
					g.setVisible(true)
					spritesShape.push(g)

					g = this.add.graphics({ fillStyle: { color: 0x77B5FE }, lineStyle: {  width: 2, color: 0x77B5FE, alpha: 1 } })

					

				}

				// Create tweens 
				// pause() by default
				// TODO JSON_properties.items MUST INCLUDE tweens INFOS
				animations.push(this.tweens.add({
					targets: sprites[2],
					x: 150,
					duration:16000,
					flipX:true,
					yoyo: true,
					repeat: -1,
				}).pause())

				animations.push(this.tweens.add({
					targets: sprites[3],
					x: 590,
					duration:16000,
					flipX:true,
					yoyo: true,
					repeat: -1,
				}).pause())

				animations.push(this.tweens.add({
					targets: sprites[4],
					x: 370,
					duration:16000,
					flipX:true,
					yoyo: true,
					repeat: -1,
				}).pause())
				
				
				
				// Create layers invisible by default and masks
				for (let i = 0; i <JSON_properties.layers.length; i++)
				{
					// each sprite pushed inside ONLY ONE Layer
					// TODO : make sprites[] a parameter in JSON_properties.layers
					const indexLayer = i // Index 0 is the layer for Design Mode
					layers.push(this.add.layer())
					if (i === 0){
						layers[indexLayer].add([sprites[0]])
					}
					if (i === 1){
						layers[indexLayer].add([sprites[2],sprites[3],sprites[4]])
					}

					graphics.push(this.add.graphics())
					graphicsMask.push(this.make.graphics().fillStyle(0x000000))
					for (let j = 0; j <JSON_properties.layers[i].zones.length; j++)
					{   
						const props = JSON_properties.layers[i].zones[j].coords
						// TODO : coords: [ 190, 100, 508, 30 ] in JSON_properties.layers MUST BE POLYGON POINTS AND NOT A RECT AS IT IS !!!
						polygon = new Phaser.Geom.Polygon([
							props[0],props[1],
							props[0]+props[2],props[1],
							props[0]+props[2],props[1]+props[3],
							props[0],props[1]+props[3],
							
						])
						layers[indexLayer].mask = new Phaser.Display.Masks.BitmapMask(this, graphicsMask[indexLayer].fillPoints(polygon.points, true))
						// invertAlpha = true -> HIDE inside masks // invertAlpha = false -> SHOW ONLY inside masks
						layers[indexLayer].mask.invertAlpha = JSON_properties.layers[i].invertAlpha
						masks[indexLayer] = layers[indexLayer].mask
						// we start in Design mode => masks are hidden 
						layers[indexLayer].clearMask()
						
						// Draw layers filled with color (invisible by default) -> fillRect + strokeRect
						graphics[indexLayer]
						.fillStyle(JSON_properties.layers[i].color, 0.4)
						.lineStyle(1, JSON_properties.layers[i].color)
						.fillPoints(polygon.points, true)
						.strokePoints(polygon.points, true)
						.setVisible(false) 
					}
				}
				this.infos = this.add.text(10, 10, '', { fontFamily: 'Arial', fontSize: 20, color: '#bb0000' })      
				
				
			},
			update: function()
			{   
				if (debug)
				{
					let debugInfos = (this.game.loop.actualFps | 0) + "fps \n" 
					debugInfos+= 'designMode: ' + designMode + "\n" 
					debugInfos+= 'displayLayers: ' + displayLayers + "\n" 
					this.infos.setText(debugInfos)
				}

				if (fullscreen){

						designMode = false

						// masks or not ?
						for (let i = 0; i < JSON_properties.layers.length; i++)
						{
							designMode ? layers[i].clearMask() : layers[i].setMask(masks[i])
						}

						// sprites play or pause => stop ?
						for (let i = 0; i < JSON_properties.items.length; i++)
						{
							const sprite = JSON_properties.items[i]
							designMode ? sprites[i].setFlipX(sprite.flipX).setFrame(0).stop() : sprites[i].play(sprite.id + '_Animation')
						}
						
						// draw sprite bound shape or not ?
						for (let i = 0; i < spritesShape.length; i++)
						{
							designMode ? spritesShape[i].setVisible(true)  : spritesShape[i].setVisible(false) 
						}
						
						// animations or not ?
						for (let i = 0; i < animations.length; i++)
						{
							designMode ? animations[i].stop(0) : animations[i].restart()
						}
						// this.scale.setGameSize(sceneWidth*2, sceneHeight*2);
						//this.scale.setGameSize(window.innerWidth, window.innerHeight)

						fullscreen = false
						this.scale.startFullscreen()
						
						
						
						
						
				}

			},
			changeDesignMode: function() 
			{
				// masks or not ?
				for (let i = 0; i < JSON_properties.layers.length; i++)
				{
					designMode ? layers[i].clearMask() : layers[i].setMask(masks[i])
				}

				// sprites play or pause => stop ?
				for (let i = 0; i < JSON_properties.items.length; i++)
				{
					const sprite = JSON_properties.items[i]
					designMode ? sprites[i].setFlipX(sprite.flipX).setFrame(0).stop() : sprites[i].play(sprite.id + '_Animation')
				}
				
				// draw sprite bound shape or not ?
				for (let i = 0; i < spritesShape.length; i++)
				{
					designMode ? spritesShape[i].setVisible(true)  : spritesShape[i].setVisible(false) 
				}
				
				// animations or not ?
				for (let i = 0; i < animations.length; i++)
				{
					designMode ? animations[i].stop(0) : animations[i].restart()
				}

			},
			showLayers: function ()
			{
				for (let i = 0; i <JSON_properties.layers.length; i++)
				{
					graphics[i].setVisible(displayLayers)
				}
			}
        }
      }
    }
  },
  methods: {
    changeDesignMode: function() {
        this.designMode = !this.designMode
        designMode = this.designMode
        this.game.scene.changeDesignMode()
    },
    showLayers: function() {
        displayLayers = !displayLayers
        this.game.scene.showLayers()
    },
	changeFullscreen: function() {
        fullscreen = true
    },
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.App {
  text-align: center;
}

#fullscreen {
	max-height: 100%;
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
}

</style>
